html {
  font-family: "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
}
body {
  /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',*/
  /*'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',*/
  /*sans-serif;*/
  max-height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}

:after,
:before {
  box-sizing: border-box;
}

:after,
:before {
  box-sizing: border-box;
}

body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
table,
td,
img,
div,
dl,
dt,
dd,
input,
figure {
  margin: 0;
  padding: 0;
}

input,
textarea,
select {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border-radius: 0;
}

body {
  -webkit-overflow-scrolling: touch;
  text-align: left;
  max-height: 100%;
}

a,
a:hover {
  text-decoration: none;
  outline: none;
}

li {
  list-style: none
}

img {
  border: none;
  max-width: 100%;
  pointer-events: none;
}

input,
select,
textarea {
  border: none;
  outline: none;
  background: none
}

textarea {
  resize: none
}

em,
i {
  font-style: normal
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal
}


/*浮动以及清除*/

.clearfix {
  *zoom: 1;
}

.clearfix::after {
  clear: both;
  display: block;
  height: 0;
  content: '';
  visibility: hidden;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

article,
aside,
dialog,
footer,
header,
section,
footer,
nav,
figure,
menu {
  display: block
}

fieldset {
  border: none;
}


/*垂直居中*/
.verticalcenter {
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

a,
a:hover,
a:active,
a:visited,
a:link,
a:focus {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  outline: none;
  background: none;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
}

a,
button,
input,
optgroup,
select,
textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a,
img {
  -webkit-touch-callout: none;
}

